.breadcrumbs {

  &__list {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #eee;
    padding-top: 32px;
    padding-bottom: 26px;
  }

  &__item {
    position: relative;
    &:not(:last-child) {
      padding-right: 35px;
    }
    &:not(:last-child)::after {
      content: "/";
      position: absolute;
      right: 10px;
      color: var(--gray-color);
    }
  }

  // &__item + &__item {
  //   padding-left: 35px;
  // }


  &__link {
    font-size: 10px;
    text-transform: uppercase;
    color: var(--gray-color);

    &--active {
      color: var(--dark-color);
    }
  }


}
