.shop-list {
  display: flex;
  align-items: center;

  &__item + &__item {
    margin-left: 25px;
  }

  &__link {
    display: inline-block;
    width: 16px;
    height: 16px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    &--user {
      background-image: url('../img/user.png');
    }
  }
}

.basket {
  width: auto;
  height: auto;
  &__icon {
    display: inline-block;
    width: 16px;
    height: 16px;
    background-image: url('../img/basket.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }


}

.search-form {
  &__btn {
    display: inline-block;
    width: 16px;
    height: 16px;
    background-image: url('../img/search.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
}

.quantity {
  display: inline-flex;
  align-items: center;
  border-radius: 25px;
  padding: 0 5px;
  height: 16px;
  font-weight: 700;
  font-size: 10px;
  color: var(--white-color);
  background-color: var(--dark-color);
}
