.catalog-filters {
  padding-top: 29px;
  padding-right: 66px;
  width: 309px;
}

.catalog-content {
  min-height: 500px;
}

.hide-filters {
  padding-left: 20px;
  margin-bottom: 18px;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;
  background-image: url('../img/cross.png');
  background-position: left center;
  background-repeat: no-repeat;
  background-size: contain;
}

.catalog-filter {
  border-top: 1px solid #eee;

  &--open {
    .catalog-filter__bottom {
      display: block;
    }

    .catalog-filter__toggle {
      &::before {
        display: none;
      }
    }
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 31px;
    padding-bottom: 29px;
    cursor: pointer;
    user-select: none;
  }

  &__caption {
    display: flex;
    align-items: center;
  }

  &__title {
    display: inline-block;
    font-weight: 700;
    font-size: 13px;
    text-transform: uppercase;
  }

  &__count {
    margin-left: 5px;
    min-width: 16px;
    transform: translateYs(-1px);
  }

  &__bottom {
    display: none;
    padding-bottom: 34px;


  }

  &__toggle {
    position: relative;
    width: 12px;
    height: 12px;
    color: #aaa;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 0;
      width: 2px;
      height: 100%;
      background-color: currentColor;
      transform: translateX(-50%);
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      width: 100%;
      height: 2px;
      background-color: currentColor;
      transform: translateX(-50%);
    }
  }

  &__items {
    columns: 2;
  }

  &__item {
    margin-bottom: 14px;

    &:nth-child(5n) {
      margin-bottom: 0;
    }
  }
}


.custom-checkbox {
  display: inline-block;
  cursor: pointer;
  user-select: none;

  &__text {
    position: relative;
    padding-left: 13px;
    font-size: 13px;
    color: var(--gray-color);

    &::before {
      content: "";
      position: absolute;
      left: -3px;
      top: 3px;
      width: 11px;
      height: 11px;
      border: 2px solid #aaa;
    }

    &::after {
      content: '\2713';
      position: absolute;
      left: 0;
      top: 0;
      // color: #aaa;
    }
  }

  &__input {
    &:checked + span {
      color: var(--dark-color);
    }

    &::before {
      border-color: var(--dark-color);
    }

    &::after {
      color: var(--dark-color);
    }
  }

}
