.hero-catalog {
  padding-top: 130px;

  &__slider {
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color: #f8f8f8;
  }
}

.catalog-slide {
  padding: 105px 108px;
  padding-bottom: 110px;

  &__title {
    max-width: 372px;
    font-weight: 700;
    font-size: 36px;
  }

  &__descr {
    margin-top: 15px;
    max-width: 385px;
    font-size: 15px;
    line-height: 28px;
    color: var(--tundora-color);
  }

  &__btns {
    display: flex;
    margin-top: 40px;
  }

  &__link {
    display: inline-block;
    padding: 15px 43px;
    font-size: 13px;
    border: 1px solid var(--dark-color);
  }

  &__link--dark {
    margin-right: 15px;
    background-color: var(--black-color);
    color: var(--white-color);
  }

  &__link--light {
    background-color: transparent;
    color: var(--dark-color);
  }
}
